.hexagon-container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.contents-hexagon {
  position: absolute;
  width: 40vw;
  height: 40vw;
  background-color: #d3d3d3;
  clip-path: polygon(50% 0, 100% 25%, 100% 75%, 50% 100%, 0 75%, 0 25%);
  display: flex;
  justify-content: center;
  align-items: center;

  .contents-hexagon__list {
    top: 50%;
    left: 50%;
    list-style: none;
    counter-increment: list-counter;
    font-size: 2vw;
    font-weight: bold;
    margin: 0;
  }
  .contents-hexagon__list::before {
    content: counter(list-counter) '. ';
  }
  .contents-hexagon__list span {
    top: 50%;
    left: 50%;
    font-size: 1vw;
    font-weight: normal;
    color: #808080;
    margin: 0;
  }
}
.contents-hexagon__contents {
  height: 45vw;
  display: inline-block;
  transform: rotate(-30deg);
  font-size: 6vh;
  font-weight: bold;
}
.contents-hexagon__contents span {
  font-size: 4vh;
}
@media screen and (max-width: 767px) {
  .contents-hexagon__list {
    width: 100vw;
    font-size: 2vh;
  }
  .contents-hexagon__list span {
    font-size: 1vh;
  }
  .contents-hexagon__contents {
    width: 45vw;
    height: 45vw;
    font-size: 2vh;
  }
  .contents-hexagon__contents span {
    font-size: 1vh;
  }
}
